.App {
  text-align: center;
}

.App-logo {
  margin: 0 auto;
  padding-bottom: 20px;
  text-align: center;
  height: 80px;
  width: 150px;
  height: 125px;
}

.App-header {
  background-color: #172F56;
  height: 200px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  line-height: 10px;
}

.App-intro {
  font-size: large;
}

.push-up {
  line-height: 8px;
}

.float {
  float: left;
  width: 100px;
  margin-left: 5px;
  visibility: collapse;
}

h1 {
  font-family: Coolvetica, sans-serif;
}

h2 {
  font-size: 20px;
  font-family: Coolvetica, sans-serif;
}

.lamp {
  height: 30px;
  width: 30px;
  border-style: solid;
  border-width: 2px;
  border-radius: 25px;
}

.typingMsg {
  font-size: 11px;
  font-style: italic;
  margin-right: 10px;
  margin-bottom: 10px;
  text-align: right;
  padding-bottom: 5px;
}

.bigred {
  font-family:"Courier New";
  font-style: italic;
  font-size: xx-large;
  color: red;
}

.blueTitle {
  font-style: italic;
  font-size: 3.9vw;
  color: #00BCD4;
}
.alertText {
  color: #FF1744;
  font-size: 2.9vw;
  margin: 3%;
}

.usersList {
  margin-left: 25px;
  margin-bottom: 5px;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: calc(80vh - 90px);
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.clearfloat {
  clear: both;
}

.userBubble {
  position:relative;
  padding:5px;
  width:50%;
  margin-bottom: 10px;
  margin-right:50px;
  border-radius:10px;
}

@font-face {
  font-family: Coolvetica;
  src: url("coolvetica-rg.ttf");
}



